import { Injectable } from '@angular/core';
import { ToastController } from '@ionic/angular';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  // private url = 'http://127.0.0.1:8000/api/';
  private url = 'https://coagra.api.smartportal.cl/';

  constructor(private http: HttpClient) {
  }

  login(rut, password) {
    const data = { rut, password };
    return this.http.post(this.url + 'session/login', data);
  }



  logout() {
    const token = localStorage.getItem('token');
    return this.http.get(this.url + 'session/logout?token=' + token);
  }

  recovery(rut) {
    return this.http.post(this.url + 'password_reset/enviar', rut);
  }

  changePassword(data) {
    const reqHeader = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': 'Bearer ' + localStorage.getItem('token')
    });
    return this.http.put(this.url + 'usuarios/cambio_pass/nueva', data, { headers: reqHeader});
  }

}
